<template>
  <div id="route_binding">
    <!--  -->
    <el-row class="console">
      <el-col :span="20">
        <el-form :inline="true" class="console-form">
          <el-form-item label="">
            <el-input v-model="consoleForm.sbrRouteName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="consoleForm.startTime" placeholder="(: 8:30)"></el-input>
          </el-form-item>
          <span class="dotted">---&nbsp;&nbsp;</span>
          <el-form-item>
            <el-input v-model="consoleForm.endTime" placeholder="(: 15:30)"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="searchHandler"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="4">
        <el-button class='add-btn' type="primary" @click="addHandler"></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <el-row class="table-wrap">
        <el-table
            :data="pageData"
            border
            stripe
            style="width: 100%"
            size="small"
            empty-text=""
            ref="routeBindForm"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @expand-change="showMore">
            <!--  -->
            <el-table-column 
              type="expand">
              <template slot-scope="props">
                <StationBindList
                  :stations="totalStations"
                  :bindStations="bindStations"
                  :routeId="props.row.id"
                  @routeReload="routeReload(props.row)"/>
              </template>
            </el-table-column>
            <el-table-column
                prop="sbrRouteName"
                label=""
                min-width="100">
            </el-table-column>
            <el-table-column
                prop="sbrDepartTime"
                label=""
                min-width="60">
            </el-table-column>
            <el-table-column
                prop="sbrStartStation"
                label=""
                min-width="80">
            </el-table-column>
            <el-table-column
                prop="sbrEndStation"
                label=""
                min-width="80">
            </el-table-column>
            <el-table-column
                prop="sbrDesc"
                label=""
                min-width="140">
            </el-table-column>
            <el-table-column
                label=""
                min-width="70">
                <template slot-scope="scope">
                    <el-button 
                    @click="editHandler(scope.row)"
                    type="text" 
                    size="small"
                    class="editBtn">
                    <i class="el-icon-edit"></i>
                    </el-button>
                    <el-button 
                    type="text" 
                    size="small"
                    class="delBtn"
                    @click="deleteHandler(scope.row)">
                    <i class="el-icon-delete"></i>
                    </el-button>
                </template>
            </el-table-column>
            
        </el-table>
    </el-row>
    <!--  -->
    <el-row class="pagination-wrap">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageOption.currentPage"
        :page-sizes="pageOption.pageSizes"
        :page-size="pageOption.pageSize"
        :layout="pageOption.layout"
        :total="pageOption.total">
      </el-pagination>
    </el-row>
    <!--  -->
    <route-dialog
      :formOption="formOption"
      :formData="formData"
      :formStations="stationNames"
      @addRoute="addRoute"
      @editRoute="editRoute"/>
  </div>
</template>
<script>
import RouteDialog from './../../components/RouteDialog.vue';
import StationBindList from './../../components/StationBindList.vue';
export default {
  data(){
      return {
        consoleForm: {
            sbrRouteName: '',
            startTime: '',
            endTime: '',
        },
        headerCellStyle: {
            'background-color': '#FAFAFA',
            'font-size': '14px',
            'line-height': '32px',
            'text-align': 'center'
        },
        cellStyle: {
            'text-align': 'center'
        },
        totalData: [],
        
        pageOption: {
            currentPage: 1,
            pageSizes: [5,10,20,50],
            pageSize: 5,
            layout: 'total, sizes, prev, pager, next, jumper',
            total: 0,
        },
        formOption: {
          type: '',
          visible: false,
          title: ''
        },
        formData: {
            sbrStartStation: '',    //
            sbrRouteName: '',       //
            sbrDesc: '',            //
            id: '',                 //id
            sbrDepartTime: '',      //
            sbrEndStation: ''       //
        },
        stationNames: [],

        totalStations: [],
        bindStations: [
          // {
          //   sbsDesc: null,            //
          //   sbsDepartTime: "6:30",    //
          //   sbsStation: "",       //
          //   stationId: 31,             //id
          //   state: 'edit',
          // },
        ],
        routeId: -1,
      }
  },
  components: {
    RouteDialog,
    StationBindList,
  },
  created(){
      this.getAllRoutes();
      this.getAllStationNames();
      this.getAllStations();
  },
  computed: {
    pageData: function(){
      let start = (this.pageOption.currentPage-1) * this.pageOption.pageSize;
      return this.totalData.filter((item,index)=>index >= start && index < start + this.pageOption.pageSize);
    },
  },
  watch: {
    totalData: function(newData){
        this.pageOption.total = newData.length;
    }
  },
  methods: {
    // 
    getAllStationNames(){
      this.$axios.get('/busposition/m/station/names')
        .then(res=>{
          this.stationNames = res.data.data;
        })
    },
    // 
    getAllRoutes(){
        this.$axios.get('/busposition/m/route/list')
          .then(res=>{
            //
            res.data.data.forEach(item=>{
              item.isLoad = false; // 
              item.stations = [];
            })
            this.totalData = res.data.data;
          })
    },
    // 
    getAllStations(){
      this.$axios.get('/busposition/m/station/list')
        .then(res=>{
          //
          res.data.data.forEach(item=>{
            let obj = {
              stationId: item.id,
              sbsStation: item.sbsStation,
              sbsDesc: item.sbsDesc,
            }
            this.totalStations.push(obj);
          })
        })
    },
    // 
    searchHandler(){
      let timeReg = /^([0-2][0-9]|[0-9]):([0-5][0-9]|[0-9])(:([0-5][0-9]|[0-9]))?$/;
      let start = this.consoleForm.startTime;
      let end = this.consoleForm.endTime;
      // 
      if(!start && end){
          this.open('','warning');
          return false;
      }else if(start && !end){
          this.open('','warning');
          return false;
      }
      if(start && end){
          if(!timeReg.test(this.consoleForm.startTime) || !timeReg.test(this.consoleForm.endTime)){
              this.open('','warning');
              return false;
          }
      }
      // 
      if(start && end){
          this.consoleForm.startTime = this.formatTime(this.consoleForm.startTime);
          this.consoleForm.endTime = this.formatTime(this.consoleForm.endTime);
      }
      // 
      this.$axios.post('/busposition/m/route/f/nameAndTime',this.consoleForm)
          .then(res=>{
              this.$message({
                  type: 'success',
                  message: '',
              })
              // 
              this.pageOption.currentPage = 1;
              this.totalData = res.data.data;
          })
    },
    //
    formatTime(strTime){
      //
      let arr = strTime.split(':');
      // 
      if(arr.length === 3){
        arr.pop(); 
      }
      // 0
      arr.forEach((item,index,arr)=>{
          if(item.length < 2){
              arr[index] = '0' + item;
          }
      })
      return arr.join(':');
    },
    // 
    addHandler(){
      this.formOption = {
        type: 'add',
        visible: true,
        title: ''
      };
      this.formData = {
        sbrStartStation: '',    //
        sbrRouteName: '',       //
        sbrDesc: '',            //
        id: '',                 //id
        sbrDepartTime: '',      //
        sbrEndStation: ''       //
      };
    },
    // 
    editHandler(route){
      this.formOption = {
        type: 'edit',
        visible: true,
        title: ''
      };
      this.formData = JSON.parse(JSON.stringify(route));
    },
    // 
    deleteHandler(route){
        this.$axios.post('/busposition/m/route/del',{id:route.id})
          .then(res=>{
              this.totalData.find((item,index,arr)=>{
                  if(item.id == route.id){
                      arr.splice(index,1);
                      if(!this.pageData.length && arr.length){
                          this.pageOption.currentPage--;
                      }
                      return true;
                  }
              });
              this.$message({
                  type: 'success',
                  message: '',
              })
          })
    },
    // 
    addRoute(route){
      this.totalData.push(route);
    },
    // 
    editRoute(route){
      let item = this.getDataById(route.id);
      // item = route;
      this.totalData.find((item,index,arr)=>{
        if(item.id == route.id){
          arr.splice(index,1,route);
          return true;
        }
      })
    },
    // 
    handleSizeChange(val){
      this.pageOption.pageSize = val;
    },
    // 
    handleCurrentChange(val){
      this.pageOption.currentPage = val;
    },
    // 
    open(msg,type) {
      this.$confirm(msg, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: type,
      })
      // .then(() => {
      //   this.$message({
      //     type: 'success',
      //     message: '!'
      //   });
      // }).catch(() => {
      //   this.$message({
      //     type: 'info',
      //     message: ''
      //   });          
      // });
    },
    //id
    getDataById(id){
      return this.totalData.find(item=>item.id === id);
    },
    // 
    passStationData(item){
      let obj = JSON.parse(JSON.stringify(item.stations));
      obj.forEach(item=>{
        item.state = 'edit';
      })
      this.bindStations.splice(0);
      Object.assign(this.bindStations,obj);
    },
    // 
    showMore(row,expandedRows){
      let isExpand = expandedRows.find(item => row === item);
      if(isExpand){
        // bindStation
        this.bindStations.splice(0);
        // id 
        let item = this.getDataById(row.id);
        // 
        if(!item.isLoad){
          //1. 
          this.$axios.post('/busposition/m/routeStation/list',{routeId: item.id})
            .then(res=>{
              //2. isLoadtrue
              item.isLoad = true;
              //3. 
              item.stations = res.data.data;
              //4. 
              this.passStationData(item);
            })
        } else {
          //4. 
          this.passStationData(item);
        }
        //5. 
        let len = expandedRows.length;
        if(len > 1){
            this.$refs.routeBindForm.toggleRowExpansion(expandedRows[0],false);                    
        }
      }
    },
    // 
    routeReload(route){
      route.isLoad = false;
    },
    getRandomIntInclusive(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min; //The maximum is inclusive and the minimum is inclusive 
    },
  },
}
</script>
<style lang="less" scoped>
#route_binding {
  padding: 15px;
  .console {
    .console-form {
      display: inline-block;
    }
    .add-btn {
      float: right;
    }
  }
  .pagination-wrap {
    margin-top: 10px;
    text-align: center;
  }
  .el-table--small td, .el-table--small th {
    padding: 5px 0;
  }
  .dotted {
      position: relative;
      top: 7px;
      left: 0;
  }
}
</style>
